import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { RESET_TOOLBAR, FONT_FAMILY } from '../../../common/constants';
import { Arrow as ArrowIcon } from 'components/Icons';
import { Tooltip } from 'components';
import DocumentEvents from 'react-document-events';
import emitter from 'common/events';
import { withStyles } from '@material-ui/styles';

class FontFamily extends PureComponent {
  domRef = React.createRef();
  state = {
    show: false,
  };

  componentDidMount() {
    emitter.on(RESET_TOOLBAR, this.reset);
  }

  componentWillUnmount() {
    emitter.removeListener(RESET_TOOLBAR, this.reset);
  }

  handleClick = () => {
    this.toggleShow();
  };

  toggleShow = () => {
    this.setState(({ show }) => {
      return { show: !show };
    });
  };

  handleChange = item => event => {
    event.stopPropagation();
    this.toggleShow();
    this.props.onChange(item);
  };

  handleOutSideClick = event => {
    if (!this.domRef.current.contains(event.target)) {
      this.reset();
    }
  };

  reset = () => {
    this.setState({
      show: false,
    });
  };

  render() {
    const { classes, rootClasses } = this.props;
    return (
      <div className={rootClasses.toolbarItem} ref={this.domRef} onClick={this.toggleShow}>
        <Tooltip title="字体">
          <div className={classes.fontItem}>{this.props.fontFamily}</div>
        </Tooltip>
        <ArrowIcon />
        {this.state.show ? (
          <div className={classes.dropdown}>
            {Object.keys(FONT_FAMILY).map(item => {
              let className = classnames(`${classes.dropdownItem}`, {
                selected: item === this.props.fontFamily,
              });
              return (
                <div key={item} className={className} onClick={this.handleChange(item)}>
                  {item}
                </div>
              );
            })}
          </div>
        ) : null}
        <DocumentEvents onClick={this.handleOutSideClick} />
      </div>
    );
  }
}
FontFamily.propTypes = {
  fontFamily: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
  onChange: PropTypes.func,
  classes: PropTypes.object,
  rootClasses: PropTypes.object,
};

const styles = {
  dropdown: {
    position: 'absolute',
    top: '25px',
    display: 'flex',
    flexDirection: 'column',
    backgroundColor: 'white',
    boxShadow: '0 0 4px 0 rgba(0,0,0,.1)',
    borderRadius: '2px',
    border: '1px solid #e0e1e5',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '12px',
    minWidth: '140px',
    maxWidth: '200px',
    zIndex: '10',
  },
  dropdownItem: {
    width: '100%',
    lineHeight: '2.5',
    textAlign: 'left',
    textIndent: '1em',

    '&.selected': {
      backgroundColor: '#f1f3f6',
    },
    '&:hover': {
      backgroundColor: '#f1f3f6',
      transition: 'background-color .3s ease',
    },
  },
  fontItem: {
    fontSize: '12px',
    height: '20px',
    width: '60px',
    textAlign: 'center',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
  },
};
export default withStyles(styles)(FontFamily);
