import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { RESET_TOOLBAR } from 'common/constants';
import { Tooltip, CheckBox, Popper } from 'components';
import { Style as StyleIcon, Vip as VipIcon, Arrow as ArrowIcon } from 'components/Icons';
import emitter from 'common/events';
import styled from 'styled-components';
import { __DEV__ } from '../../../common/constants';
import { pcBridge } from '../../../bridges';
import client from '../../../common/client';
import defaultImage from '../../../images/default.png';
import simpleImage from '../../../images/simple.png';
import { ClickAwayListener } from '../../../components';

const Strategy = [
  ['右侧布局', 'logic_right'],
  ['左侧布局', 'logic_left'],
  // ['组织结构朝下', 'logic_bottom'],
  // ['组织结构朝上', 'logic_top'],
  ['平衡布局', 'logic_left_right'],
  // ['目录布局', 'logic_bottom_catalog'],
];

export default class LayoutLogic extends PureComponent {
  iconRef = React.createRef();
  static propTypes = {
    onChange: PropTypes.func.isRequired,
    strategy: PropTypes.string,
    rootClasses: PropTypes.object,
    lineType: PropTypes.string,
  };

  constructor(props) {
    super(props);
    this.state = {
      show: false,
    };
  }

  componentDidMount() {
    emitter.on(RESET_TOOLBAR, this.reset);
  }

  componentWillUnmount() {
    emitter.removeListener(RESET_TOOLBAR, this.reset);
  }

  handleChange = item => event => {
    event.stopPropagation();
    this.toggleShow();
    this.props.onChange(item, this.props.lineType);
  };

  handleChangeCanvas = lineType => {
    if (client.isPC()) {
      if (lineType === 'simple') {
        const isVip = pcBridge.IsVip();
        if (isVip) {
          this.props.onChange(this.props.strategy, lineType);
        }
        return;
      }
      this.props.onChange(this.props.strategy, lineType);
      return;
    }
    if (lineType === 'simple' && !__DEV__) {
      mindmapEditor.emit('controller:check:vip', isVip => {
        if (isVip) {
          this.props.onChange(this.props.strategy, lineType);
        }
      });
      return;
    }
    this.props.onChange(this.props.strategy, lineType);
  };

  toggleShow = () => {
    this.setState(({ show }) => {
      return { show: !show };
    });
  };

  reset = () => {
    this.setState({
      show: false,
    });
  };

  render() {
    const { rootClasses, lineType, strategy } = this.props;
    return (
      <div className={rootClasses.toolbarItem} onClick={this.toggleShow}>
        <Tooltip title="样式">
          <WrapperSelect ref={this.iconRef}>
            <StyleIcon />
            样式
          </WrapperSelect>
        </Tooltip>
        <ArrowIcon />
        <Popper
          open={this.state.show}
          anchorEl={this.iconRef.current}
          style={{ zIndex: 1000 }}
          forceUpdate={this.state.show}
        >
          <ClickAwayListener onClickAway={this.reset}>
            <StyledDropDown>
              <CanvasSelect selectedValue={lineType} onChange={this.handleChangeCanvas} />
              <StyledTitle>布局方式</StyledTitle>
              <WrapperLayout>
                {Strategy.map(([item, name]) => (
                  <CheckBox
                    label={item}
                    checked={strategy === name}
                    key={item}
                    onClick={this.handleChange(name)}
                  />
                ))}
              </WrapperLayout>
            </StyledDropDown>
          </ClickAwayListener>
        </Popper>
      </div>
    );
  }
}

const WrapperSelect = styled.div`
  display: flex;
  justify-content: space-evently;
  align-items: center;
  width: 42px;
  font-size: 12px;
`;

const WrapperLayout = styled.div`
  display: flex;
  flex-direction: column;
`;

const WrapperPanel = styled.div`
  display: flex;
  justify-content: space-between;
  width: 186px;
`;

const CanvasSelectWrapper = styled.div`
  margin-bottom: 29px;
`;

const CanvasSelect = props => {
  const { selectedValue, ...rest } = props;
  return (
    <CanvasSelectWrapper>
      <StyledTitle>请选择画布</StyledTitle>
      <WrapperPanel>
        <ImagePanel
          src={defaultImage}
          content="经典"
          {...rest}
          selected={selectedValue === 'default'}
          value="default"
        />
        <ImagePanel
          src={simpleImage}
          content="简洁"
          {...rest}
          selected={selectedValue === 'simple'}
          value="simple"
          Icon={VipIcon}
        />
      </WrapperPanel>
    </CanvasSelectWrapper>
  );
};
CanvasSelect.propTypes = {
  selectedValue: PropTypes.string,
  onChange: PropTypes.func,
};

const StyledTitle = styled.h3`
  color: #666666;
  font-size: 12px;
  font-weight: 400;
  margin-bottom: 8px;
`;

const StyledPanel = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  color: ${props => (props.selected ? '#4C94FF' : '#666666')};
`;

const StyledDropDown = styled.div`
  display: flex;
  flex-direction: column;
  background-color: white;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  padding: 19px 12px;
  border: 1px solid #e0e1e5;
  font-size: 12px;
`;
const StyledImg = styled.img`
  width: 87px;
  height: 62px;
  border: 1px solid;
  border-color: ${props => (props.selected ? '#4C94FF' : '#E0E1E5')};
`;

const ImagePanel = ({ content, src, value, onChange, selected, Icon }) => (
  <StyledPanel selected={selected} onClick={() => onChange(value)}>
    <StyledImg selected={selected} src={src} alt={content} width="87" height="62" />
    <span>
      {content}
      {Icon && <Icon />}
    </span>
  </StyledPanel>
);

ImagePanel.propTypes = {
  src: PropTypes.string,
  content: PropTypes.string,
  value: PropTypes.string,
  onChange: PropTypes.func,
  selected: PropTypes.bool,
  Icon: PropTypes.func,
};
