import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Tooltip, InnerLink, Popper } from 'components';
import { HyperLink as HyperLinkIcon } from 'components/Icons';

export default class InnerLinkComponent extends PureComponent {
  static propTypes = {
    onClick: PropTypes.func,
    link: PropTypes.object,
    id: PropTypes.string,
    disabled: PropTypes.bool,
    rootClasses: PropTypes.object,
  };

  static defaultProps = {
    disabled: false,
  };
  constructor() {
    super();
    this.innerLink = React.createRef();
    this.anchorElRef = React.createRef();
    this.state = {
      open: false,
    };
  }

  componentDidMount() {
    document.addEventListener('click', this.handleOutSideClick);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.handleOutSideClick);
  }

  handleOutSideClick = event => {
    if (
      this.anchorElRef.current.contains(event.target) ||
      (this.innerLink.current && this.innerLink.current.contains(event.target)) ||
      this.state.open === false
    ) {
      return;
    }

    this.setState({
      open: false,
    });
    mindmapEditor.__uistatus__.focus = '';
  };

  handleClick = (e, link) => {
    if (this.getDisable()) return;
    this.props.onClick(e, link);
    this.setState({
      open: false,
    });
    mindmapEditor.__uistatus__.focus = '';
    e.stopPropagation();
  };

  handleOpen = () => {
    if (this.getDisable()) return;
    this.setState({
      open: true,
    });
    mindmapEditor.__uistatus__.focus = 'link';
  };

  getDisable = () => {
    const { id, link, disabled } = this.props;
    if (disabled) return true;
    // 如果是笔记内链，那么禁止编辑
    if (link && link.name) return true;
    return id === 'root' || id == null;
  };

  render() {
    const { id, link, rootClasses } = this.props;
    const value = link ? link.value : undefined;
    let isSelected = false;
    if (link && !link.isInnerLink && value) {
      isSelected = true;
    }
    const disable = this.getDisable();

    return (
      <div
        ref={this.anchorElRef}
        onClick={this.handleOpen}
        className={`${rootClasses.toolbarItem} ${isSelected ? rootClasses.toolbarItemActive : ''}`}
      >
        <React.Fragment>
          <Tooltip title="链接">
            <HyperLinkIcon disabled={disable} />
          </Tooltip>
          <Popper
            open={!disable && this.state.open}
            anchorEl={this.anchorElRef.current}
            placement="bottom"
            style={{ zIndex: 1000 }}
          >
            <InnerLink
              ref={this.innerLink}
              id={id}
              value={value}
              onClick={(event, val) => this.handleClick(event, val)}
            />
          </Popper>
        </React.Fragment>
      </div>
    );
  }
}
