import React from 'react';
import PropTypes from 'prop-types';
import { Tooltip } from 'components';
import {
  Bold as BoldIcon,
  Italic as ItalicIcon,
  UnderLine as UnderLineIcon,
  Strike as DeleteLineIcon,
} from 'components/Icons';

const emptyFunc = () => {};

export const Bold = React.memo(({ hotKey, fontWeight, onClick, disabled, rootClasses }) => (
  <Tooltip title={`加粗 | ${hotKey}+B`}>
    <div
      className={`${rootClasses.toolbarItem} ${
        fontWeight === 'bold' ? rootClasses.toolbarItemActive : ''
      }`}
      onClick={disabled ? emptyFunc : onClick}
    >
      <BoldIcon disabled={disabled} />
    </div>
  </Tooltip>
));
Bold.displayName = Blob;
Bold.propTypes = {
  hotKey: PropTypes.string,
  fontWeight: PropTypes.string,
  onClick: PropTypes.func,
  disabled: PropTypes.bool,
  rootClasses: PropTypes.object,
};

export const Italic = React.memo(({ hotKey, fontStyle, onClick, disabled, rootClasses }) => (
  <Tooltip title={`斜体 | ${hotKey}+I`}>
    <div
      className={`${rootClasses.toolbarItem} ${
        fontStyle === 'italic' ? rootClasses.toolbarItemActive : ''
      }`}
      onClick={disabled ? emptyFunc : onClick}
    >
      <ItalicIcon disabled={disabled} />
    </div>
  </Tooltip>
));

Italic.displayName = Italic;
Italic.propTypes = {
  hotKey: PropTypes.string,
  fontStyle: PropTypes.string,
  onClick: PropTypes.func,
  disabled: PropTypes.bool,
  rootClasses: PropTypes.object,
};

export const UnderLine = React.memo(
  ({ textDecoration, onClick, disabled, hotKey, rootClasses }) => {
    return (
      <Tooltip title={`下划线 | ${hotKey}+U`}>
        <div
          className={`${rootClasses.toolbarItem} ${
            textDecoration && textDecoration.includes('underline')
              ? rootClasses.toolbarItemActive
              : ''
          }`}
          onClick={disabled ? emptyFunc : onClick}
        >
          <UnderLineIcon disabled={disabled} />
        </div>
      </Tooltip>
    );
  },
);
UnderLine.displayName = UnderLine;
UnderLine.propTypes = {
  hotKey: PropTypes.string,
  textDecoration: PropTypes.oneOfType([PropTypes.array, PropTypes.string]),
  onClick: PropTypes.func,
  disabled: PropTypes.bool,
  rootClasses: PropTypes.object,
};

export const DeleteLine = React.memo(
  ({ textDecoration, onClick, disabled, hotKey, rootClasses }) => (
    <Tooltip title={`删除线 | ${hotKey}+E`}>
      <div
        className={`${rootClasses.toolbarItem} ${
          textDecoration && textDecoration.includes('line-through')
            ? rootClasses.toolbarItemActive
            : ''
        }`}
        onClick={disabled ? emptyFunc : onClick}
      >
        <DeleteLineIcon
          iconName="strike"
          iconClass={rootClasses.styleEditor}
          disabled={disabled}
          disabledName="strike_disable"
        />
      </div>
    </Tooltip>
  ),
);

DeleteLine.displayName = DeleteLine;
DeleteLine.propTypes = {
  hotKey: PropTypes.string,
  textDecoration: PropTypes.oneOfType([PropTypes.array, PropTypes.string]),
  onClick: PropTypes.func,
  disabled: PropTypes.bool,
  rootClasses: PropTypes.object,
};
