import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { RESET_TOOLBAR } from 'common/constants';
import { Tooltip } from 'components';
import { TextColor as TextColorIcon, Arrow as ArrowIcon } from 'components/Icons';
import emitter from 'common/events';
import Documentevents from 'react-document-events';
import dynamic from 'next/dynamic';

// const LazyColorPicker = React.lazy(() => import(/* webpackChunkName: "color-picker" */ '../../../components/LazyColorPicker/'));
const LazyColorPicker = dynamic(() => import('../../../components/LazyColorPicker'));
const emptyFunc = () => {};

class ColorPicker extends PureComponent {
  domRef = React.createRef();
  static propTypes = {
    onChange: PropTypes.func.isRequired,
    color: PropTypes.string,
    disabled: PropTypes.bool,
    rootClasses: PropTypes.object,
  };

  constructor(props) {
    super(props);
    this.state = {
      show: false,
      selectedItem: props.color || 'red',
    };
    this.firstInit = true;
  }

  componentDidMount() {
    emitter.on(RESET_TOOLBAR, this.reset);
  }

  componentWillUnmount() {
    emitter.removeListener(RESET_TOOLBAR, this.reset);
  }

  toggleShowColorPicker = e => {
    e.stopPropagation();
    this.toggleShow();
  };

  toggleShow = () => {
    this.setState(({ show }) => {
      return { show: !show };
    });
  };

  handleChange = (color, event) => {
    event.stopPropagation();
    this.setState({ selectedItem: color.hex });
    this.cacheColor = color.hex;
  };

  handleOutSideClick = event => {
    if (!this.domRef.current.contains(event.target)) {
      this.reset();
      if (this.cacheColor) {
        this.props.onChange(this.cacheColor);
        this.cacheColor = null;
      }
    }
  };

  reset = () => {
    this.setState({
      show: false,
    });
  };

  checkColor = () => {
    return this.state.selectedItem.startsWith('#') ? this.state.selectedItem : 'red';
  };
  handleClick = () => {
    this.props.onChange(this.checkColor());
  };

  render() {
    const { disabled, rootClasses } = this.props;
    const colorLineStyle = {
      width: 15,
      height: 2,
      position: 'absolute',
      top: 20,
      left: 8,
      backgroundColor: this.checkColor(),
    };
    const containerStyle = {
      position: 'relative',
    };
    return (
      <Tooltip title="文字颜色">
        <div
          className={rootClasses.toolbarItem}
          ref={this.domRef}
          style={containerStyle}
          onClick={disabled ? emptyFunc : this.handleClick}
        >
          <span style={colorLineStyle} />
          <TextColorIcon disabled={disabled} style={{ width: 18, height: 18, marginLeft: '3px' }} />
          <ArrowIcon
            disabled={disabled}
            onClick={disabled ? emptyFunc : this.toggleShowColorPicker}
          />
          {this.state.show ? (
            <LazyColorPicker color={this.state.selectedItem} onChangeComplete={this.handleChange} />
          ) : null}
          <Documentevents onMouseDown={this.handleOutSideClick} />
        </div>
      </Tooltip>
    );
  }
}

export default ColorPicker;
