import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { undoStack } from 'common/undoStack';
import { redoStack } from 'common/redoStack';
import { RESET_TOOLBAR } from 'common/constants';
import { Tooltip } from 'components';
import { Undo as UndoIcon, Redo as RedoIcon } from 'components/Icons';
import emitter from 'common/events';

export default class UndoRedo extends Component {
  static propTypes = {
    rootClasses: PropTypes.object,
  };
  componentDidMount() {
    emitter.on(RESET_TOOLBAR, this.reset);
  }

  componentWillUnmount() {
    emitter.removeListener(RESET_TOOLBAR, this.reset);
  }

  undo = () => {
    if (!undoStack.canUndo()) return;
    mindmapEditor.handleUndo();
  };

  redo = () => {
    if (!redoStack.canRedo()) return;
    mindmapEditor.handleRedo();
  };

  reset = () => {
    undoStack.reset();
    redoStack.reset();
    this.forceUpdate();
  };

  render() {
    const { rootClasses } = this.props;
    const canUndo = undoStack.canUndo();
    const canRedo = redoStack.canRedo();
    return (
      <div style={{ display: 'flex' }}>
        <div className={rootClasses.toolbarItem}>
          <Tooltip title="撤销">
            <UndoIcon onClick={this.undo} disabled={!canUndo} />
          </Tooltip>
        </div>
        <div className={rootClasses.toolbarItem}>
          <Tooltip title="重做">
            <RedoIcon onClick={this.redo} disabled={!canRedo} />
          </Tooltip>
        </div>
      </div>
    );
  }
}
