import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { RESET_TOOLBAR } from 'common/constants';
import { Tooltip } from 'components';
import { Arrow } from 'components/Icons';
import classnames from 'classnames';
import { range, map } from 'lodash';
import DocumentEvents from 'react-document-events';
import emitter from 'common/events';

const fontSizeList = map(range(6, 21), item => item * 2);

const fontSizeStyle = {
  fontSize: '12px',
  width: 20,
};

class FontSizePicker extends PureComponent {
  containerRef = React.createRef();
  static propTypes = {
    onChange: PropTypes.func.isRequired,
    fontSize: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    rootClasses: PropTypes.object,
  };

  constructor(props) {
    super(props);
    this.state = {
      show: false,
    };
  }

  componentDidMount() {
    emitter.on(RESET_TOOLBAR, this.reset);
  }

  componentWillUnmount() {
    emitter.removeListener(RESET_TOOLBAR, this.reset);
  }

  handleChange = item => event => {
    event.stopPropagation();
    this.toggleShow();
    this.props.onChange(item);
  };

  toggleShow = () => {
    this.setState(({ show }) => {
      return { show: !show };
    });
  };

  handleOutSideClick = e => {
    if (!this.containerRef.current.contains(e.target)) {
      this.reset();
    }
  };

  reset = () => {
    this.setState({
      show: false,
    });
  };

  render() {
    const { fontSize, rootClasses } = this.props;
    return (
      <div className={rootClasses.toolbarItem} ref={this.containerRef} onClick={this.toggleShow}>
        <Tooltip title="字号">
          <div style={fontSizeStyle}>{parseInt(fontSize, 10)}</div>
        </Tooltip>
        <Arrow />
        {this.state.show ? (
          <div className={rootClasses.dropdown}>
            {fontSizeList.map(item => {
              let className = classnames(rootClasses.dropdownItem, {
                selected: item === parseInt(this.props.fontSize, 10),
              });
              return (
                <div key={item} className={className} onClick={this.handleChange(item)}>
                  {item}
                </div>
              );
            })}
          </div>
        ) : null}
        <DocumentEvents onClick={this.handleOutSideClick} />
      </div>
    );
  }
}

export default FontSizePicker;
