export const getDisabledStatus = (selectedNodes, disbaledType) => {
  if (!disbaledType) return false;
  const isSelected = selectedNodes.size !== 0;
  const isMultSelected = selectedNodes.size > 1;
  const isRoot = selectedNodes.has('root');
  switch (disbaledType) {
    case 1:
      return !isSelected;
    case 2:
      return isMultSelected;
    case 3:
      return !isSelected || isMultSelected;
    case 7:
      return !isSelected || isMultSelected || isRoot;
    default:
      return false;
  }
};
