import React, { Component } from 'react';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import DocumentEvents from 'react-document-events';
import UndoRedo from './UndoRedo';
import { Bold, Italic, UnderLine, DeleteLine } from './StyleEditor';
import ColorPicker from './ColorPicker';
import FontSizePicker from './FontSizePicker';
import FontFamily from './FontFamily';
import { Popper } from 'components';
import {
  More,
  InsertBrotherComponent,
  InsertChildComponent,
  BackTocenterComponent,
  GapLine,
  getDisabledStatus,
  InsertParentComponent,
} from './baseComponent';
import RemarkContainer from '../RemarkContainer';
import client from 'common/client';
import _ from 'lodash';
// import FileUploadComponent from './FileUploadComponent';
import InnerLinkComponent from './InnerLinkComponent';
import { getOuterWidth, immutableTransFromCache } from '../../common/util';
import LayoutLogic from './LayoutLogic';
import { withStyles } from '@material-ui/styles';
import { __DEV__ } from '../../common/constants';

function _getSelectNode(set, nodes) {
  const selectNode = [...set][0];
  return nodes.find(item => item.id === selectNode) || {};
}

const getSelecteNode = immutableTransFromCache(_getSelectNode);

const PaddingHelper = 60;
class Toolbar extends Component {
  constructor(props) {
    super(props);
    this.HotKey = client.isMac() ? 'Command' : 'Ctrl';
    this.handleResize = _.debounce(this.handleResize, 300);
    this.state = {
      showMore: false,
    };
    this.toolbarRef = React.createRef();
    this.cacheIconListMap = []; // 完整的 toolbar 组件
    this.iconListMap = []; // 显示的 toolbar 组件
    this.remainIconList = []; // 未直接显示的 toolbar 组件，在更多种
    this.cacheItemWidth = [];
    this.moreIconRef = React.createRef();
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    this.recordMoreIconWidth();
    const htmlCollection = this.toolbarRef.current.children;
    // didmount 后不一定马上能获得宽高
    setTimeout(() => {
      [].forEach.call(htmlCollection, item => {
        const outerWidth = getOuterWidth(item);
        this.cacheItemWidth.push(outerWidth);
      });
      this.handleResize();
    });
    // 编辑状态下 触发更新
    mindmapEditor.on('controller:update:toolbar', this.handleResize);
  }

  componentDidUpdate() {
    this.recordMoreIconWidth();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
    mindmapEditor.removeListener('controller:update:toolbar', this.handleResize);
  }

  static getDerivedStateFromProps(nextProps) {
    return {
      cacheSelectedNode: nextProps.nodeContainer.state.selectedNodes,
    };
  }

  renderItemList = (toolbar, classes, { selectedNodes, nodes }) => {
    if (!this.cacheIconListMap.length) {
      this.cacheIconListMap = [
        {
          Component: UndoRedo,
          data: () => ({ key: 'UndoRedo' }),
        },
        {
          Component: InsertBrotherComponent,
          data: () => ({
            disbaledType: 7,
            onClick: mindmapEditor.insertBrotherNode,
            key: 'InsertBrotherComponent',
          }),
        },
        {
          Component: InsertChildComponent,
          data: () => ({
            disbaledType: 3,
            onClick: mindmapEditor.insertChildNode,
            key: 'InsertChildComponent',
          }),
        },
        {
          Component: InsertParentComponent,
          data: () => ({
            disbaledType: 7,
            onClick: mindmapEditor.insertParentNode,
            key: 'InsertParentComponent',
          }),
        },
        {
          Component: BackTocenterComponent,
          data: () => ({
            hotKey: this.HotKey,
            onClick: mindmapEditor.scrollToCenter,
            key: 'BackTocenterComponent',
          }),
        },
        {
          Component: GapLine,
          data: () => ({
            key: 'GapLine',
          }),
        },
        {
          Component: FontSizePicker,
          data: ({ fontSize }) => ({
            onChange: mindmapEditor.handleFontSize,
            key: 'FontSizePicker',
            fontSize,
          }),
        },
        {
          Component: FontFamily,
          data: ({ fontFamily }) => ({
            onChange: mindmapEditor.handleFontFamily,
            key: 'FontFamily',
            fontFamily,
          }),
        },
        {
          Component: Bold,
          data: ({ fontWeight }) => ({
            onClick: mindmapEditor.toggleBold,
            disbaledType: 1,
            hotKey: this.HotKey,
            key: 'Bold',
            fontWeight,
          }),
        },
        {
          Component: Italic,
          data: ({ fontStyle }) => ({
            onClick: mindmapEditor.toggleItalic,
            disbaledType: 1,
            hotKey: this.HotKey,
            key: 'Italic',
            fontStyle,
          }),
        },
        {
          Component: UnderLine,
          data: ({ textDecoration }) => ({
            onClick: mindmapEditor.toggleUnderline,
            disbaledType: 1,
            hotKey: this.HotKey,
            key: 'UnderLine',
            textDecoration,
          }),
        },
        {
          Component: DeleteLine,
          data: ({ textDecoration }) => ({
            onClick: mindmapEditor.toggleDeleteline,
            disbaledType: 1,
            hotKey: this.HotKey,
            key: 'DeleteLine',
            textDecoration,
          }),
        },
        {
          Component: GapLine,
          data: () => ({ key: 'GapLine1' }),
        },
        {
          Component: ColorPicker,
          data: ({ color }) => ({
            onChange: this.handleColor,
            disbaledType: 1,
            key: 'ColorPicker',
            color,
          }),
        },
        {
          Component: GapLine,
          data: () => ({
            key: 'GapLine2',
          }),
        },
        {
          Component: RemarkContainer,
          data: ({ selectNode }) => ({
            key: 'RemarkContainer',
            disbaledType: 7,
            data: selectNode,
          }),
        },
        {
          Component: GapLine,
          data: () => ({
            key: 'GapLine3',
          }),
        },
        {
          Component: InnerLinkComponent,
          data: ({ link, selectNode }) => ({
            onClick: this.handleSubmitLink,
            disbaledType: 7,
            key: 'InnerLinkComponent',
            id: selectNode ? selectNode.id : null,
            link,
          }),
        },
        {
          Component: LayoutLogic,
          data: ({ strategy, lineType }) => ({
            onChange: this.handleLayoutChange,
            key: 'LayoutLogic',
            strategy,
            lineType,
          }),
        },
      ];
      this.iconListMap = this.cacheIconListMap.slice();
    }
    const copyToolbar = { ...toolbar };
    const selectNode = getSelecteNode(selectedNodes, nodes);
    const link = selectNode && selectNode.link ? selectNode.link : null;
    copyToolbar.link = link;
    copyToolbar.selectNode = selectNode;
    return this.iconListMap.map(config => {
      const configResult = config.data(copyToolbar);
      return React.createElement(config.Component, {
        ...configResult,
        disabled:
          getDisabledStatus(selectedNodes, configResult.disbaledType) || mindmapEditor.isEditing,
        rootClasses: classes,
      });
    });
  };

  recordMoreIconWidth = () => {
    if (this.cacheToolbarMoreIconWidth) return;
    const moreIcon = document.getElementById('toolbar-more');
    this.cacheToolbarMoreIconWidth = moreIcon ? moreIcon.offsetWidth : 0;
  };

  handleLayoutChange = (...args) => {
    mindmapEditor.handleLayoutStrategy(...args);
  };

  handleResize = () => {
    let SumWidth = 0;
    if (
      !document.getElementById('toolbar') ||
      document.getElementById('toolbar').offsetWidth === 0
    ) {
      __DEV__ && console.log('not found toolbar');
      return;
    }
    const cacheToolbarWidth =
      parseInt(document.getElementById('toolbar').offsetWidth, 10) - PaddingHelper;
    __DEV__ && console.log('cacheToolbarWidth', cacheToolbarWidth);
    this.cacheToolbarWidth = cacheToolbarWidth;
    const cacheIconListMap = this.cacheIconListMap;
    const cacheToolbarMoreIconWidth = this.cacheToolbarMoreIconWidth;
    // eslint-disable-next-line no-unused-vars
    let iconListMap;
    let remainIconList = [];
    this.cacheItemWidth.every((item, index) => {
      SumWidth += item;
      if (SumWidth + cacheToolbarMoreIconWidth > cacheToolbarWidth) {
        iconListMap = cacheIconListMap.slice(0, index);
        remainIconList = cacheIconListMap.slice(index) || [];
        return false;
      }
      return true;
    });
    if (!iconListMap) {
      iconListMap = cacheIconListMap.slice();
    }
    if (!remainIconList.length) {
      this.setState({
        showMore: false,
      });
    }
    this.iconListMap = iconListMap;
    this.remainIconList = remainIconList;
    this.forceUpdate();
  };

  gapComponent = index => {
    const { classes } = this.props;
    return (
      <div className={`${classes.toolbarItem} ${classes.toolbarSplit}`} key={`split-${index}`} />
    );
  };

  _canToggle = () => {
    return !mindmapEditor.isEditing && this.props.nodeContainer.state.selectedNodes.size !== 0;
  };

  handleSubmitLink = (e, link) => {
    if (!link.id || !link.value) {
      mindmapEditor.removeLink(link);
      return;
    }
    mindmapEditor.insertLink(link);
  };

  handleColor = color => {
    if (!this.state.cacheSelectedNode.size) return;
    mindmapEditor.handleColor(this.state.cacheSelectedNode, color);
  };

  handleShowMore = () => {
    this.setState({
      showMore: true,
    });
  };

  renderRemainList = (toolbar, classes, { selectedNodes, nodes }) => {
    const selectNode = getSelecteNode(selectedNodes, nodes);
    const link = selectNode && selectNode.link ? selectNode.link : null;
    const copyToolbar = { ...toolbar };
    copyToolbar.link = link;
    copyToolbar.selectNode = selectNode;
    return this.remainIconList.map(config => {
      const configResult = config.data(copyToolbar);
      return React.createElement(config.Component, {
        ...configResult,
        disabled: getDisabledStatus(selectedNodes, configResult.disbaledType),
        rootClasses: classes,
      });
    });
  };

  handleClickOutside = event => {
    const moreContainer = document.getElementById('more-container');
    if (
      (this.moreIconRef.current && this.moreIconRef.current.contains(event.target)) ||
      (moreContainer && moreContainer.contains(event.target)) ||
      mindmapEditor.__uistatus__.focus
    ) {
      return;
    }
    if (this.state.showMore === true) {
      // 注意这里加setTimeout 是优先触发弹窗里的事件，然后再隐藏弹窗
      setTimeout(() => {
        this.setState({
          showMore: false,
        });
      });
    }
  };

  handleReadOnly = () => {
    this.props.toolbarContainer.handleReadOnly();
  };

  render() {
    const {
      nodeContainer,
      toolbarContainer: {
        state: { toolbar },
      },
      classes,
    } = this.props;
    return (
      <div className={classes.root} id="toolbar">
        <div className={classes.toolbar} ref={this.toolbarRef}>
          {this.renderItemList(toolbar, classes, nodeContainer.state)}
          {this.remainIconList.length > 0 && (
            <More onClick={this.handleShowMore} ref={this.moreIconRef} rootClasses={classes} />
          )}

          <Popper
            open={this.state.showMore}
            anchorEl={this.moreIconRef.current}
            style={{ zIndex: 1000 }}
            forceUpdate={this.state.showMore} // 强制更新使得当resize 时弹窗重新计算位移
          >
            <div className={classes.more} id="more-container">
              {this.renderRemainList(toolbar, classes, nodeContainer.state)}
            </div>
          </Popper>
          <DocumentEvents onClick={this.handleClickOutside} />
        </div>
      </div>
    );
  }
}

Toolbar.propTypes = exact({
  nodeContainer: PropTypes.object,
  toolbarContainer: PropTypes.object,
  classes: PropTypes.object,
});

const styles = {
  root: {
    transform: 'translateZ(0)',
    position: 'fixed',
    top: '0',
    zIndex: '255',
    width: '100%',
    borderTop: '1px solid rgba(0,0,0,.05)',
    borderBottom: '1px solid rgba(0,0,0,.05)',
    backgroundColor: '#fff',
    display: 'flex',
    alignItems: 'center',
    WebkitUserSelect: 'none',
    userSelect: 'none',
    '&:after': {
      content: ' ',
      display: 'block',
      height: '0',
      clear: 'both',
    },
    '&:hover': {
      cursor: 'pointer',
    },
  },
  toolbar: {
    position: 'relative',
    display: 'flex',
    alignItems: 'center',
    padding: '8px 0 8px 29px',
  },
  more: {
    display: 'flex',
    alignItems: 'center',
    flexWrap: 'wrap',
    background: '#fff',
    boxShadow: '0 1px 16px rgba(90,109,122,.4)',
    padding: '8px',
  },
  toolbarSplit: {
    width: '1px',
    height: '18px',
    backgroundColor: 'rgba(0,0,0,.05)',
    cursor: 'none',
    padding: '0 !important',
  },
  toolbarItem: {
    display: 'flex',
    alignItems: 'center',
    padding: '5px',
    margin: '0 15px 0 0',
    cursor: 'pointer',
    '&:hover': {
      backgroundColor: '#f1f3f6',
      borderRadius: '1px',
      borderColor: '#f1f3f6',
    },
  },
  toolbarItemActive: {
    backgroundColor: '#f1f3f6',
    borderRadius: '1px',
    borderColor: '#f1f3f6',
  },
  arrow: {
    width: 7,
    height: 5,
  },
  arrowWrapper: {
    width: '15px',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  dropdown: {
    position: 'absolute',
    top: '25px',
    display: 'flex',
    flexDirection: 'column',
    width: '80px',
    backgroundColor: 'white',
    boxShadow: '0 0 4px 0 rgba(0,0,0,.1)',
    borderRadius: '2px',
    border: '1px solid #e0e1e5',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '12px',
    zIndex: '10',
  },
  dropdownItem: {
    width: '100%',
    lineHeight: '2.5',
    textAlign: 'center',
    '&.selected': {
      backgroundColor: '#f1f3f6',
    },
    '&:hover': {
      backgroundColor: '#f1f3f6',
      transition: 'background-color .3s ease',
    },
  },
  styleEditor: {
    borderColor: 'transparent',
    cursor: 'pointer',
    width: '18px',
    height: '18px',
  },
};
export default withStyles(styles)(Toolbar);
