import React, { Component } from 'react';
import PropTypes from 'prop-types';
import RemarkComponent from './RemarkComponent';
import { Tooltip, Popper } from 'components';
import { Remark as RemarkIcon } from 'components/Icons';

export default class RemarkContainer extends Component {
  static propTypes = {
    disabled: PropTypes.bool,
    data: PropTypes.object,
    rootClasses: PropTypes.object,
  };

  state = {
    open: false,
  };

  anchorRef = React.createRef();

  saveRemark = idAndRemark => {
    mindmapEditor.saveRemark(idAndRemark);
  };

  handleBlur = () => {
    this.setState({
      open: false,
    });
    mindmapEditor.__uistatus__.focus = '';
  };

  handleOpen = event => {
    event.preventDefault();
    event.stopPropagation();
    if (this.isDisable()) return;
    mindmapEditor.__uistatus__.focus = 'remark';
    this.setState({
      open: true,
    });
  };

  isDisable = () => {
    return this.props.disabled;
  };

  render() {
    const {
      data: { id, remark: value = '' },
      rootClasses,
    } = this.props;
    return (
      <React.Fragment>
        <Tooltip title="备注">
          <div
            className={`${rootClasses.toolbarItem} ${value ? rootClasses.toolbarItemActive : ''}`}
            ref={this.anchorRef}
            onClick={this.handleOpen}
          >
            <RemarkIcon disabled={this.isDisable()} />
          </div>
        </Tooltip>
        <Popper open={this.state.open} anchorEl={this.anchorRef.current} style={{ zIndex: 1000 }}>
          <RemarkComponent
            saveRemark={this.saveRemark}
            id={id}
            value={value}
            autoFocus={true}
            onBlur={this.handleBlur}
          />
        </Popper>
      </React.Fragment>
    );
  }
}
